.backdrop {
    z-index: 9;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
  
    background: rgba(0, 0, 0, 0.5);
    

    &.is-hidden {
        opacity: 0;
        pointer-events: none;
      }
  }

.modal {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(1);
  
    background: #ffffff;
    background-image: linear-gradient(
        to right,
        rgba(211, 217, 250, 0.2),
        rgba(209, 209, 212, 0.2)
      ),
      url('../images/modal/modal-ice-cream-450x610px.jpg');
      
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
  
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);


    @media screen and (max-width: 1199px) {
      max-width: 450px;
      width: 90vw;
      max-height: 609px;
      height: 90vh;
    }

    @media screen and (min-width: 1200px) {
      min-width: 528px;
      min-height: 581px;
    }

    &__title {
        padding: 150px 40px 2px 40px;
        margin-bottom: 10px;
        text-align: center;
        font-family: Titan One;
        font-size: 32px;
        line-height: 1.15;
        text-align: center;
        letter-spacing: 0.2em;
        color: $white;
        text-shadow: 4px 3px 4px rgba(78, 78, 78, 0.5);
        text-transform: uppercase;
        }

    &__label {
        display: flex;
        flex-direction: column;
        margin-left: 40px;
        margin-right: 40px;
        margin-bottom: 10px;
        
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.01em;
        
        color: gray;
        

        &:last-of-type {
            margin-bottom: 20px;
            }
        }

    &__wrap {
        position: relative;
        }
    
    &__input{
        width: 100%;
        height: 40px;
        margin-top: 4px;
        padding-left: 42px;
        
        border: 1px solid rgba(63, 63, 63, 0.2);
        box-shadow: 4px 3px 4px rgba(78, 78, 78, 0.5);
        box-sizing: border-box;
        border-radius: 4px;
        
        outline: none;
        
        transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

            &:focus,
            &:hover {
                border-color: $accent-color;
                cursor: pointer;
            }
        }

    &__icon {
        position: absolute;
        align-items: center;
        justify-content: center;
        display: flex;
        fill: gray;
        
        margin-left: 11px;
        
        transform: translateY(-160%);

        .modal__input:focus + &,
        .modal__input:hover + & {
            fill: $pink-icecream-bg;
            }
        }

    &__submit-button {
        
        display: block;
        margin-left: auto;
        margin-right: auto;
        padding-right: 54px;
        padding-left: 54px;

        
        width: 200px;
        height: 50px;
        margin-left: auto;
        margin-right: auto;
        
        border: none;
        
        font-weight: bold;
        font-size: 18px;
        line-height: 1.875;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.06em;
        color: gray;
        
        background: $white;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
        border-radius: 25px;
        
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

            &:focus,
            &:hover {
                background: $button-hover;
                cursor: pointer;
            }
        }

    .backdrop.is-hidden & {
        transform: translate(-50%, -50%) scale(0.5);
        }

    &__close {
        position: absolute;
        right: 8px;
        top: 8px;
    
        width: 30px;
        height: 30px;
    
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        background-color: $white;
        border-radius: 50%;
        border: 1px solid rgba(0, 0, 0, 0.1);
        fill: $accent-color;

        &:hover {
            background-color: $pink-light-bg;
            }
    }

    &__select-form {
  
        text-align: center;
        align-items: center;
        justify-content: center;
        margin-left: 40px;
        margin-right: 40px;
        margin-bottom: 30px;
        
    }
    
    &__select-text {
        display: block;
        color: $pink-dark-bg;
        text-shadow: 4px 3px 4px rgba(0,0,0,0.5);
        
        font-family: Titan One;
        font-weight: 400;
        font-size: 14px;
        margin-bottom: 10px;
    
      @media screen and (min-width: 768px){
        font-size: 18px;
      }
    
      @media screen and (min-width: 1200px){
        font-size: 20px;
      }
    }
  
    &__select-ice {
        width: 100%;
        font-family: Titan One;
        font-size: 16px;
    
      @media screen and (min-width: 320px) {
        height: 30px;
      }
    
      @media screen and (min-width: 768px) {
        height: 40px;
      }
    }
  }

  
  
